/*-------------------------*/
/* $Color 
---------------------------*/
/* background color */
$primary:#6487f5;
$secondary: #ff638f;
$black: #484848;
$grey: #cbcbcb;
$white: #fefefe;
.bg-color-primary {
  background-color: $primary;
}

.bg-color-secondary {
  background-color: $secondary;
}

.bg-color-black {
  background-color: $black;
}

.bg-color-grey {
  background-color: $grey;
}

.bg-color-white {
  background-color: $white;
}

/* text color */

.color-primary {
  color: $primary;
}

.color-secondary {
  color: $secondary;
}

.color-black {
  color: $black;
}

.color-grey {
  color: $grey;
}

.color-white {
  color: $white;
}

/*-------------------------*/
/* $Typography 
---------------------------*/
/* Globel */

body, html {
  font-family: 'Helvetica', 'roboto', arial, sans-serif;
}

/* heading */

h1, h2, h3, h4, h5, h6 {
  margin: 0;
  color: #484848;
}

h1 {
  font-size: 80px;
  font-weight: 800;
  font-family: 'Helvetica', 'roboto', arial, sans-serif;
  margin-top: 24px;
  margin-bottom: 24px;
}

h2 {
  font-size: 48px;
  font-weight: 600;
  font-family: 'Helvetica', 'roboto', arial, sans-serif;
  margin-top: 24px;
  margin-bottom: 24px;
}

h3 {
  font-size: 40px;
  font-weight: 600;
  font-family: 'Helvetica', 'roboto', arial, sans-serif;
  line-height: 48px;
  margin-top: 24px;
  margin-bottom: 24px;
}

h4 {
  font-size: 24px;
  font-weight: 600;
  font-family: 'Helvetica', 'roboto', arial, sans-serif;
  line-height: 32px;
  margin-top: 24px;
  margin-bottom: 24px;
}

h5 {
  font-size: 20px;
  font-weight: 400;
  font-family: 'Helvetica', 'roboto', arial, sans-serif;
  line-height: 1;
  margin-top: 24px;
  margin-bottom: 16px;
}

h6 {
  font-size: 18px;
  font-weight: 400;
  font-family: 'Helvetica', 'roboto', arial, sans-serif;
  line-height: 32px;
  margin-top: 24px;
  margin-bottom: 16px;
}

p {
  font-size: 16px;
  font-weight: 400;
  font-family: 'Helvetica', 'roboto', arial, sans-serif;
  line-height: 32px;
  letter-spacing: 0;
  margin-bottom: 16px;
}

.subtitle {
  font-size: 48px;
  font-weight: 900;
  font-family: 'Raleway','Helvetica', 'roboto', arial, sans-serif;
  margin-top: 24px;
  margin-bottom: 24px;
}

@media (max-width: 768px) {
  .subtitle {
    font-size: 40px;
  }
}

/* link */

a {
  color: #484848;
  text-decoration: none;
  &:hover, &:active, &:fucus {
    color: #484848;
    text-decoration: none;
  }
}

/* list */

ul {
  list-style: none;
  margin: 0;
  padding: 0;
}

