/*------------------------- */
/* $Elements */
/*--------------------------- */

/* background */
/*--------------------- */

.bg {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-image: url("../images/urban-long-exposure-night.jpg");
  background-size: cover;
  background-position: center;
  &:after {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: linear-gradient(60deg, #4e4f95, #c87db4);
    opacity: 0.6; } }

/* lean-bg */
/*--------------------- */

.lean-bg--top:before, .lean-bg--bottom:after {
  content: "";
  position: absolute;
  width: 100%;
  height: 180px;
  background-color: $white; }

@media (max-width: 768px) {
  .lean-bg--top:before, .lean-bg--bottom:after {
    height: 80px; } }

.lean-bg--top:before {
  top: 0;
  transform: skewY(-5deg) translateY(-50%); }

.lean-bg--bottom:after {
  bottom: 0;
  transform: skewY(-5deg) translateY(50%); }

.lean-bg--top {
  margin-top: 160px; }

@media (max-width: 768px) {
  .lean-bg--top {
    margin-top: 80px; } }

.lean-bg--bottom {
  margin-bottom: 160px; }

@media (max-width: 768px) {
  .lean-bg--bottom {
    margin-bottom: 80px; } }

.lean-bg--no-margin {
  margin: 0; }

/* btn */
/*--------------------- */
/* normal */

.btn {
  display: inline-block;
  vertical-align: top;
  position: relative;
  margin: 0;
  height: 36px;
  line-height: 36px;
  padding: 0 16px;
  min-width: 64px;
  box-sizing: border-box;
  font-size: 14px;
  text-align: center;
  font-weight: 500;
  font-family: 'Raleway', 'Helvetica', 'roboto', arial, sans-serif;
  letter-spacing: 0;
  text-decoration: none;
  background-color: #d1d2d3;
  border: 0;
  border-radius: 18px;
  outline: 0;
  cursor: pointer;
  overflow: hidden;
  user-select: none;
  user-drag: none;
  transition: all 0.3s; }

.btn--primary, .btn--secondary {
  color: $white; }

.btn {
  &.btn--primary {
    background-color: #e881a7;
    &:hover, &:active {
      background-color: #dd6488; } }
  &.btn--secondary {
    background-color: inherit;
    border: 1px solid #e881a7;
    color: #e881a7;
    &:hover, &:active {
      background-color: #e881a7;
      color: $white; } }
  &.btn--lg {
    font-size: 20px;
    line-height: 56px;
    height: 56px;
    font-weight: 400;
    border-radius: 28px;
    padding: 0 64px; }
  &.btn--md {
    font-size: 18px;
    line-height: 48px;
    height: 48px;
    font-weight: 400;
    border-radius: 24px;
    padding: 0 64px; }
  &.btn--sm {
    font-size: 14px;
    line-height: 32px;
    height: 32px;
    font-weight: 400;
    border-radius: 16px;
    padding: 0 32px; } }

.btn-group .btn {
  margin-left: 8px;
  margin-right: 8px; }

.btn-group--center {
  text-align: center; }

/* icon btn */

.icon-btn {
  display: inline-block;
  width: 32px;
  height: 32px;
  line-height: 32px;
  font-size: 24px;
  .icon {
    display: block;
    text-align: center;
    transition: all 0.3s;
    &:hover {
      transform: translateY(-20%); } } }

/* text btn */

.text-btn {
  display: inline-block;
  vertical-align: top;
  position: relative;
  margin: 0;
  box-sizing: border-box;
  font-size: 14px;
  font-weight: 400;
  font-family: 'Raleway', 'Helvetica', 'roboto', arial, sans-serif;
  letter-spacing: 0;
  text-decoration: none;
  color: #484848;
  &.text-btn--primary {
    color: #e881a7; }
  &.text-btn--lg {
    font-size: 20px;
    line-height: 48px;
    height: 48px;
    font-weight: 900;
    &:before {
      content: "";
      position: absolute;
      bottom: 0;
      left: 0;
      width: 0%;
      height: 2px;
      background-color: #e881a7;
      transition: all 0.3s; }
    &:hover:before {
      width: 100%; } } }

/* image */
/*--------------------- */

.image {
  width: 100%;
  display: block; }

/* imgbox */
/*--------------------- */

.imgbox {
  width: 100%;
  overflow: hidden; }

.imgbox__inner {
  width: 100%;
  padding-top: 100%;
  position: relative;
  &.imgbox__inner--4-3 {
    padding-top: calc(100% * 3 / 4); }
  .image {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-size: cover;
    background-position: center; } }

/*------------------------- */
/* $Header */
/*--------------------------- */

.header {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 100;
  transition: all 0.3s;
  height: 200px; }
.header--colored {
  background-color: $white;
  box-shadow: 0 3px 5px rgba(0, 0, 0, 0.1);
  .menu-toggle span {
    background-color: #e881a7; }
  .navbar__social-bar .icon-btn {
    color: #e881a7; } }

/* navbar */
/*--------------------- */

.navbar {
  height: 80px;
  border: 1px solid #fff; }
.navbar--collapse {
  margin-right: -16px;
  margin-left: -16px; }

.navbar__social-bar {
  padding: 24px;
  float: left;
  .icon-btn {
    color: $white; } }

@media (max-width: 768px) {
  .navbar__social-bar {
    padding: 12px; } }

.navbar__nav {
  float: right; }

/* menu-toggle */
/*--------------------- */

.menu-toggle {
  display: block;
  padding: 24px;
  padding-top: 26px;
  span {
    display: block;
    height: 2px;
    width: 32px;
    background-color: $white;
    margin-bottom: 8px;
    transition: all 0.3s; }
  &:hover span {
    &:nth-child(1) {
      transform: translateX(-20%); }
    &:nth-child(3) {
      transform: translateX(20%); } } }

@media (max-width: 768px) {
  .menu-toggle {
    padding: 12px;
    padding-top: 16px; } }

/* popup-menu */
/*--------------------- */

.popup-menu {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: $white;
  transition: all 0.3s;
  opacity: 0;
  visibility: hidden; }

#popup-overlay:target {
  opacity: 1;
  visibility: visible; }

.popup-menu__close {
  padding: 24px;
  width: 80px;
  height: 80px;
  float: right;
  transition: all 0.3s;
  margin-right: -16px;
  &:hover {
    transform: rotate(180deg); }
  span {
    width: 32px;
    height: 2px;
    background-color: #e881a7;
    position: absolute;
    top: 50%;
    left: 50%;
    &:nth-child(1) {
      transform: translate(-50%, -50%) rotate(45deg); }
    &:nth-child(2) {
      transform: translate(-50%, -50%) rotate(-45deg); } } }

@media (max-width: 768px) {
  .popup-menu__close {
    padding: 12px;
    width: 56px;
    height: 56px; } }

.popup-menu__item {
  display: block;
  text-align: center;
  font-family: 'Raleway', 'Helvetica', 'roboto', arial, sans-serif;
  font-size: 40px;
  font-weight: 900;
  color: #484848;
  line-height: 80px;
  transition: all 0.3s;
  &:hover {
    color: #e881a7; } }

@media (max-width: 768px) {
  .popup-menu__item {
    font-size: 24px;
    line-height: 48px; } }

/*------------------------- */
/* $About */
/*--------------------------- */

.hero {
  padding-top: 20vh;
  padding-bottom: 20vh; }

@media (max-width: 480px) {
  .hero {
    padding-top: 80px;
    padding-bottom: 80px; } }

.hero__heading {
  font-size: 120px;
  font-weight: 900;
  color: $white; }

@media (max-width: 768px) {
  .hero__heading {
    font-size: 48px; } }

.hero__bg {
  position: absolute;
  top: 80px;
  width: 100%;
  height: 100%;
  background-image: url("../images/portrait.png");
  background-size: 400px;
  background-repeat: no-repeat;
  background-position: top right;
  left: 50%;
  transform: translateX(-50%);
  max-width: 1140px; }

@media (max-width: 768px) {
  .hero__bg {
    background-size: 320px; } }

@media (max-width: 480px) {
  .hero__bg {
    background-size: 200px; } }

/*------------------------- */
/* $Quote */
/*--------------------------- */

.quote {
  padding-top: 80px;
  padding-bottom: 80px; }

@media (max-width: 768px) {
  .quote {
    padding-top: 40px;
    padding-bottom: 40px; } }

.quote__content {
  text-align: center;
  font-size: 18px;
  line-height: 40px;
  color: #484848; }

.quote__icon {
  width: 64px;
  height: 64px;
  border-radius: 50%;
  background-color: #e881a7;
  text-align: center;
  line-height: 64px;
  font-size: 32px;
  color: $white;
  margin-right: auto;
  margin-left: auto;
  margin-bottom: 24px; }

.quote__text {
  text-align: center;
  font-size: 18px;
  line-height: 40px;
  margin-bottom: 24px; }

.quote__name span {
  color: #e881a7; }

/*------------------------- */
/* $Skill */
/*--------------------------- */

.skill, .skill-group {
  padding-top: 80px;
  padding-bottom: 80px; }

@media (max-width: 768px) {
  .skill {
    padding-top: 40px;
    padding-bottom: 40px; } }

.skill-card {
  padding-left: 32px;
  margin-bottom: 32px; }

.skill-card__inner {
  background-color: $white;
  border-radius: 4px;
  box-shadow: 0 3px 10px rgba(0, 0, 0, 0.5);
  padding: 24px;
  padding-left: 48px; }

.skill-card__title {
  color: #e881a7;
  font-family: 'Raleway','Helvetica', 'roboto', arial, sans-serif;
  margin-top: 0;
  margin-bottom: 8px; }

.skill-card__text {
  margin: 0; }

.skill-card__icon {
  width: 64px;
  height: 64px;
  border-radius: 50%;
  box-shadow: 0 3px 10px rgba(0, 0, 0, 0.5);
  position: absolute;
  left: 0;
  top: 50%;
  transform: translateY(-50%);
  background-color: #f3f4f5;
  .image {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    max-width: 60%;
    max-height: 60%; } }

@media (max-width: 768px) {
  .skill-card__icon {
    top: 0;
    left: 50%;
    transform: translate(-50%, -50%); }
  .skill-card {
    padding: 0;
    margin-top: 48px; }
  .skill-card__inner {
    padding: 24px;
    padding-top: 48px; }
  .skill-group {
    padding-bottom: 0; } }

/*------------------------- */
/* $Experience */
/*--------------------------- */

.experience {
  padding-top: 80px;
  padding-bottom: 80px; }

@media (max-width: 768px) {
  .experience {
    padding-top: 40px;
    padding-bottom: 40px; } }

/* timeline */
/*--------------------- */

.timeline-group {
  max-width: 960px;
  margin-right: auto;
  margin-left: auto;
  margin-top: 120px; }

.timeline {
  width: 4px;
  height: 100%;
  background-color: #e881a7;
  position: absolute;
  top: 0;
  left: 50%;
  transform: translateX(-50%);
  box-shadow: 0 3px 10px rgba(0, 0, 0, 0.5); }

.timeline-cards__row {
  &:nth-child(odd) {
    display: flex;
    justify-content: flex-start; }
  &:nth-child(even) {
    display: flex;
    justify-content: flex-end; } }

.timeline-card {
  padding: 24px;
  background-color: $white;
  border-radius: 4px;
  width: calc(50% - 24px);
  box-shadow: 0 3px 10px rgba(0, 0, 0, 0.5);
  &:before {
    content: "";
    width: 16px;
    height: 16px;
    border-radius: 50%;
    box-shadow: 0 3px 10px rgba(0, 0, 0, 0.5);
    background-color: #e881a7;
    position: absolute;
    top: 50%; } }

.timeline-cards__row {
  &:nth-child(odd) > :before {
    transform: translate(50%, -50%);
    right: -24px; }
  &:nth-child(even) > :before {
    transform: translate(-50%, -50%);
    left: -24px; } }

.timeline-card__text {
  margin: 0; }

@media (max-width: 768px) {
  .timeline {
    left: 0; }
  .timeline-card {
    width: calc(100% - 24px);
    margin-bottom: 24px; }
  .timeline-cards__row {
    &:last-child .timeline-card {
      margin-bottom: 0; }
    &:nth-child(odd) {
      justify-content: flex-end;
      > :before {
        transform: translate(-50%, -50%);
        left: -24px; } } } }

/*------------------------- */
/* $Work */
/*--------------------------- */

.work {
  padding-top: 80px;
  padding-bottom: 120px; }

@media (max-width: 768px) {
  .work {
    padding-top: 40px;
    padding-bottom: 80px; } }

/* work-list */
/*--------------------- */

.work-list {
  padding-top: 80px; }

.work-item {
  margin-bottom: 160px; }

.work-item__text {
  padding-top: 24px; }

.work-item__text--right {
  text-align: right; }

.work-item__title {
  font-size: 40px;
  font-family: 'Raleway', 'Helvetica', 'roboto', arial, sans-serif;
  color: #484848; }

.work-item__title--right {
  margin-right: -80px; }

.work-item__title--left {
  margin-left: -80px; }

.work-item__intro {
  font-size: 16px;
  line-height: 32px; }

@media (max-width: 768px) {
  .work-item {
    margin-bottom: 48px; }
  .work-item__text--right {
    text-align: left; }
  .work-item__title {
    font-size: 24px;
    line-height: 32px;
    margin-top: 0;
    margin-bottom: 8px; }
  .work-item__title--right {
    margin-right: 0; }
  .work-item__title--left {
    margin-left: 0; } }

/*------------------------- */
/* $Contact */
/*--------------------------- */

.contact {
  padding-top: 80px;
  padding-bottom: 80px; }

@media (max-width: 768px) {
  .contact {
    padding-top: 40px;
    padding-bottom: 40px; } }

/* form */
/*--------------------- */

.form {
  max-width: 600px;
  margin-right: auto;
  margin-left: auto;
  input {
    &[type="text"], &[type="tel"], &[type="email"] {
      padding: 8px 16px;
      display: inline-block;
      border: 1px solid #cdcdcd;
      border-radius: 4px;
      vertical-align: middle;
      box-sizing: border-box;
      width: 100%;
      margin-bottom: 16px;
      background-color: inherit;
      color: $white; } }
  textarea {
    padding: 8px 16px;
    display: inline-block;
    border: 1px solid #cdcdcd;
    border-radius: 4px;
    vertical-align: middle;
    box-sizing: border-box;
    width: 100%;
    margin-bottom: 16px;
    background-color: inherit;
    color: $white;
    resize: none; }
  input {
    &[type="text"]:focus, &[type="tel"]:focus, &[type="email"]:focus {
      outline: 0;
      border: 1px solid #e881a7; } }
  textarea:focus {
    outline: 0;
    border: 1px solid #e881a7; }
  label {
    margin-bottom: 8px;
    display: inline-block;
    vertical-align: middle;
    color: $white; } }

.form__btn-group {
  margin-top: 32px; }

/* divide-text */
/*--------------------- */

.divide-text {
  text-align: center;
  margin-top: 80px;
  margin-bottom: 80px;
  font-size: 28px;
  line-height: 32px;
  color: $white; }

/* info */
/*--------------------- */

.info {
  text-align: center; }

.info__email {
  margin-bottom: 16px;
  a {
    color: #e881a7;
    font-size: 18px;
    font-weight: 400; } }

.info__phone {
  margin-bottom: 16px;
  a {
    color: $white;
    font-size: 24px;
    font-weight: 400; } }

.info__social-bar .icon-btn {
  color: #e881a7; }

/*------------------------- */
/* $footer */
/*--------------------------- */

.footer {
  padding-top: 40px;
  padding-bottom: 40px; }

.final-text {
  text-align: center;
  max-width: 600px;
  margin-right: auto;
  margin-left: auto;
  color: $white; }

.copyright {
  text-align: center;
  color: $white;
  font-size: 14px; }

